<template>
    <div class="userEdit-container">
        <!-- 用户设置 -->
        <div class="content">
            <van-form>
                <van-field
                readonly
                    v-model="username"
                    name="用户名"
                    label="用户名"
                    placeholder="用户名"
                    :rules="[{ required: true, message: '请填写用户名' }]"
                />
                <van-field
                readonly
                    v-model="password"
                    type="password"
                    name="密码"
                    label="密码"
                    placeholder="密码"
                    :rules="[{ required: true, message: '请填写密码' }]"
                />
                <van-field readonly v-model="tel" type="tel" label="手机号" :rules="[{required: true, message: '请输入手机号'}]" />
                <van-radio-group readonly v-model="radio" direction="horizontal" id="xinbie">
                    <span>性别</span>
                    <van-radio readonly name="1">男</van-radio>
                    <van-radio readonly name="2">女</van-radio>
                </van-radio-group>
                <div>
                    <van-button type="danger" 
                    @click.stop="handleCLick"
                    >退出登录</van-button>
                </div>
            </van-form>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            username: '',
            password: '',
            tel: "",
            radio: '1',
        };
    },
    mounted() {
        const user = this.$store.state.loginUser.user;
        if(user) {
            this.username = user.username;
            this.password = user.password;
            this.tel = user.phoneNumber;
            this.radio = user.radio
        }
    },
    methods: {
        handleCLick() {
            this.$store.commit("loginUser/setUser", null)
            this.$router.push({
                name: "login",
            })
        }
    },
}
</script>

<style lang="scss" scoped>
.userEdit-container {
        height: calc(100vh - 60px);
        display: flex;
        justify-content: center;
        .content{
            text-align: center;
            padding-top: 20px;
        }
    }
    #xinbie{
        margin-left: 15px;
        margin-top: 5px;
        margin-bottom: 15px;
        &>span{
            margin-right: 20px;
        }
    }
    .info-btn{
        margin-right: 10px;
    }
</style>